import React from 'react'

// Styles
// import './Status.scss'

const Status = React.createClass({
  showMore: function (e) {
    e.preventDefault();
    console.log('button was clicked');
  },
  render () {
    return (
      <section className='section-item about-component'>
        <h3 className='section-title'>Status</h3>
        <div className='section-content'>
          <p>I'm currently advising <a className='animated-line' href='http://www.dh.com/' target='_blank'>D+H Financial Solutions</a>, a leading FinTech company, on the design and development of <a className='animated-line' href='http://www.dh.com/product/barometer' target='_blank'>Barometer</a>, a configurable, SaaS platform that provides insight into how well employees understand and apply regulations and policies.  I am not currently available for work but more than happy to hear from you and chat about ways we might work together in the future.</p>
        </div>
      </section>
    )
  }
})

export default Status



