import React from 'react'

// Styles
import './About.scss'

const About = React.createClass({
  showMore: function (e) {
    e.preventDefault();
    console.log('button was clicked');
  },
  render () {
    return (
      <section className='section-item about-component'>
        <h3 className='section-title'>About</h3>
        <div className='section-content'>
          <p>Nick is a privacy lawyer and software development consultant working at the intersection of law and data in Toronto. He advises organizations that are creating new, disruptive products in conditions of uncertainty on matters related to product design, development and data law, specifically: privacy law, access to information, data security and information management.</p>
        </div>
      </section>
    )
  }
})

export default About



